<template>
    <div class="result">
        <div class="title">{{ results[resultIndex].title }}</div>
        <div class="desc">
            {{ results[resultIndex].desc }}
        </div>
    </div>
</template>

<script>
export default {
    name:'ResultComponent',
    props:[
        "results",
        "totCorrect"
    ],
    computed:{
        resultIndex(){
            let index = 0
            this.results.forEach((el, i) => {
                if(el.min <= this.totCorrect && el.max >= this.totCorrect){
                    index = i
                }
            });
            return index
        }
    }
}
</script>